.wrapper__switch {
  padding: 30px;
  background-color: lightblue;

  --pillar--padding: 20px;
  --pillar__logo--width: 64px;
}

.wrapper__pillar {
  position: absolute;
  top: 0;
  left: calc(
    50% - calc(var(--pillar__logo--width) / 2) - var(--pillar--padding)
  );
}
.pillar {
  padding: var(--pillar--padding);
  padding-top: 60px;
  background-color: black;
}
.pillar__logo {
  height: auto;
  width: var(--pillar__logo--width);
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 60px;
}
.footer__logo {
  height: auto;
  width: var(--pillar__logo--width);
}

.wrapper__background {
  width: 100%;
  background-color: black;
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 1) 50%
    ),
    url("/home/arnell/React/upgraded-umbrella/src/assets/images/bg.png");
  background-position: top;
  background-repeat: no-repeat;
  color: white;
}
